<template>
    <div class="nav">
      <router-link class="nav_item " to="/goods" @click="tap(1)">点餐
        <div class="line"></div> 
      </router-link> 
      <router-link class="nav_item" to="/ratings"  @click="tap(2)">评价
        <div class="line"></div> 
      </router-link> 
      <router-link class="nav_item" to="/sellers"  @click="tap(3)">商家
        <div class="line"></div>
      </router-link> 
    </div>
</template>
<script>
export default {
  name: 'appnav',
  data () {
    return {
      left:null,
      first: null,
      second: false,
      third: false
    }
  },
  methods:{
    
      
    
  }
}
</script>
<style>
.nav{
	display:flex;
	width:100%;
	height: 40px;
	line-height: 40px;
	border-bottom: 1px solid #ddd;
}
.nav_item{
  position: relative;
	flex: 1;
	text-align: center;
  font-size: 1rem;
}
.router-link-active .line{
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;
  display: inline-block;
  width: 2.5rem;
  height: 2px;
  background: #EA1;
  transition:  0s ease 0s;
}
</style>


