<template>
  <div class="apphead">
    <div class="dis-box justify-content-between row">
      <div class="dis-box flex-1">
        <i class="iconfont icon-qianjin-copy ce pl-5"></i>
        <search-bar :searchMsg="ss" class="ml-15 mr-15"></search-bar>  
      </div>
      <div class="dis-box mr-10">
        <div class="cf b mr-10 fs16">拼单</div>
        <span class="iconfont icon-gengduo cf"></span>  
      </div>
    </div>
    <div class="dis-box justify-content-between row">
      <div class="dis-box">
        <!-- <i class="iconfont icon-maidanglao logo "></i> -->
        <img :src="poiInfo.pic_url" alt="">
        <div class="cf fw-b fs22">{{poiInfo.name}}</div>
      </div>
      <div class="dis-box justify-content-center flex-direction-column mr-10">
        <i class="iconfont icon-star c-yellow fs24"></i>
        <div class="cf fs16 pd-5">收藏</div> 
      </div>      
    </div>
    <div class="dis-box row justify-content-between">
      <div class="dis-box flex-1">
        <div class="shouye">首</div>
        <div class="tips over-flow-line-1">新用户立减17元，首次使用美团支付最高再减3元</div>  
      </div>
      
      <div class="iconfont icon-qianjin pr-5 fs12"></div>
     
      
    </div>
    
  </div>
</template>

<script>
import SearchBar from '../../components/SearchBar'
export default {
  name: 'apphead',
  props:{
    poiInfo:{
      type: Object,
      default:{}
    }
  },
  components:{
    SearchBar
  },
  created(){
    // console.log(555,this.poiInfo)
  },
  data () {
    return {
      ss:'搜索店内商品'
    }
  }
}
</script>

<style lang="scss" scoped>
.apphead{
  position: relative;
  width:100%;
	background: url('../../assets/img/bg2.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.row{
  padding: 10px 0;
  &:last-child *{
    font-size: 12px;
    color: #fff;
  }
}

img{
  height: 3.222222rem;
  width: 3.222222rem;
  margin: 0 10px;
}
.shouye{
  background: $red;
  color: #fff;
  font-size: 12px;
  // height: 0.833333rem;
  // width: 0.833333rem;
  margin: 0 10px;
  padding: 0 2px;
}
.tips{
  white-space: nowrap;
}
</style>


