<template>
  <div id="app">
    <!-- 头部 -->
    <app-head :poiInfo="poiInfo"></app-head>
    <!-- 导航 -->
    <app-nav></app-nav>
    <!-- 内容 -->
    <router-view></router-view>
    
  </div>
</template>

<script>
import AppHead from './view/header/AppHead'
import AppNav from './view/nav/AppNav'
export default {
  name: 'App',
  components: {
    AppHead,
    AppNav
  },
  data(){
    return{
      poiInfo:{}
    }
  },
  created(){
    fetch("api/goods").then(res=>{
      return res.json()
    }).then(response=>{
      // console.log(response)
      if(response.code==0){
        this.poiInfo = response.data.poi_info
        // console.log(this.poiInfo)
      }
    })
  }
}
</script>

<style>

</style>



