<template>
<transition name="food-detail">
	<div class="prodetail" v-show="detail_show">
		<div class="imgwrap">
			<img :src="food.picture" alt="">
		</div>
		<div class="top dis-box justify-content-between pd-20">
			<div class="close" @click="close">
				<i class="iconfont icon-guanbi1 cf"></i>
			</div>
			<div class="right disbox">
				<i class="iconfont icon-zhuanfa"></i>
				<i class="iconfont icon-gengduo ml-10"></i>
			</div>
		</div>
		<div class="middle">
			<div class="fw-b">{{food.name}}</div>
			<div class="fs12 c9">{{food.month_saled_content}}</div>
			<div class="dis-box justify-content-between pb-0">
				<div class="c-red fw-b fs26 pt-5">￥{{food.min_price}}</div>
				<div class="addproduct iconfont icon-gouwuche1 fs22"
					v-show="!this.food.count" @click="increase">
					<span class="pl-10 fs16">加入购物车</span>
				</div>
			<cart-control :food="food" v-show="this.food.count"></cart-control>
			</div>
		</div>
		<div class="rating">
			<div class="dis-box justify-content-between bb pb-20">
				<div>外卖好评（好评度<span class="c-red">{{food.rating.like_ratio}}</span>）</div>
				<div class="dis-box">
					<span class="c9 pr-10">{{food.rating.comment_count}}条评论</span>
					<i class="iconfont icon-qianjin c9"></i>
				</div>
			</div>
			<div class="content ">
				<div class="item dis-box  align-items-top ptb-20 bb">
	  			<div class="img_wrap"><img src="" alt=""></div>
	  			<div class="flex-1 pl-10 ">
	  				<div class="dis-box justify-content-between">
	  					<span>匿名用户</span>
	  					<span>2018.06.26</span>
	  				</div>
	  				<div class="pt-10">点的牛奶，到手边牛腩</div>
	  				<div class="user_food">
	  					<img src="" alt="">
	  				</div>
	  			</div>	
				</div>

			</div>
		</div>
		
	</div>
</transition>
</template>

<script>
import Vue from 'vue'
import CartControl from '../cartcontrol/CartControl'
export default {
	props:{
		food:{
			type: Object
		}
	},
	components:{
		CartControl
	},
	data () {
		return {
			detail_show: false
		}
	},
	methods:{
		showproduct(){
			this.detail_show = true
		},
		close(){
			this.detail_show = false
		},
		increase(){
	  		Vue.set(this.food,"count",1)
  		}
	}
}
</script>
<style lang="scss">
.food-detail-enter-active, .food-detail-leave-active {
  transition:  0.6s
}
.food-detail-enter, .food-detail-leave-to {
  transform: translateX(100%);
}
.prodetail{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 95%;
	background: $body_grey;
	z-index: 5;
	overflow: auto;
	margin-bottom: 4.222222rem;
	.imgwrap{
		width: 100%;
		background: #fff;
		img{
			width:100%;
			object-fit: cover;
		}
	}
	.top{
		box-sizing: border-box;
		position: absolute;
		top: 5px;
		width: 100%;
		*{
			font-size: 100%;
		}
		i{
			height: 2rem;
			width: 2rem;
			background: #444;
			opacity: 0.8;
			border-radius: 50%;
			color: #fff;
			padding:5px;
		}
		.close i{font-size: 100%;}
		.right i{font-size: 120%;}

	}
	.middle{
		padding: 1.111111rem;
		background: #fff;
		*{
			padding-bottom: 0.555556rem;
		}
		.addproduct{
			padding: 0.277778rem;
			border-radius: 1.111111rem;
			background: $cmain;
			color: #333;
			*{font-size: 0.777778rem;}
		}
	}
	.rating{
		padding: 1.111111rem;
		background: #fff;
		margin-top: 0.833333rem;
		.img_wrap{
			width: 2.777778rem;
			height: 2.777778rem;
			border-radius: 50%;
			background: $cmain;
		}
		.user_food{
			margin-top: 0.833333rem;
			width: 7.777778rem;
			height: 7.777778rem;
			background: $cmain;
		}
	}
}	

</style>



