<template>
<div>
  <div class="goods dis-box align-items-top" >
  	<!-- 左边分类列表 -->
  	<div class="leftnav" ref="menuScroll">
	  	<ul>
	  		<li @click="tap(0)">
	  			<div class="dis-box">
	  				<img :src="zhuanchang.tag_icon" class="pr-5">
	  				<div>{{zhuanchang.tag_name}}</div>

	  			</div>
	  		</li>
	  		<li v-for="(it,index) in goods" @click="tap(index+1)" :key="index">
	  			<div class="dis-box">
		  			<img :src="it.icon" alt="" v-if="it.icon">
		  			<div class="overflow-line-2 pl-5">{{it.name}}</div>	
	  			</div>
	  			<span v-show="calculateCount(it.spus)">{{calculateCount(it.spus)}}</span>
	  		</li>
	  	</ul>	
  	</div>
  	
  	<!-- 右边商品列表 -->
  	<div class="rightview flex-1" ref="foodScroll" @scroll="listScroll">
		<ul>
			<li class="food-list-dot">
				<div class="topimg zhuanchang_hook" v-for="it in zhuanchangList">
					<img :src="it.pic_url" >
				</div>
	  		</li>
			<li v-for="it in goods" class="food-list-dot" >
				<div class="pd-10"><span class="tit">{{it.name}}</span></div>
				
				<div class="item dis-box" v-for="item in it.spus" @click="showProduct(item)">
					<div class="goodsimg"><img :src="item.picture" alt=""></div>
					<div class="flex-1">
						<div class="title">{{item.name}}</div>
						<div class="ptb-10">月售{{item.month_saled}}
							<span class="pl-15">赞{{item.praise_num}}</span>
						</div>
						<div class="dis-box justify-content-between pr-5">
							<div>￥{{item.min_price}}</div>
							<cart-control :food="item"></cart-control>
						</div>
					</div>	
				</div>
			</li>
		</ul>
  	</div>
  	<ProductDetail ref="proDetail" :food="selcetshow"></ProductDetail>
  </div>
  <!-- 底部购物车 -->
  <shop-cart :poi_info="poi_info" :selectFoods="selectFoods"></shop-cart>
 </div>
</template>
	
<script>
import ShopCart from '../shopcart/ShopCart'
import CartControl from '../cartcontrol/CartControl'
import ProductDetail from '../productdetail/ProductDetail'
export default { 
  name: 'goods',
  components:{
  	ShopCart,CartControl,ProductDetail
  },
  data () {
    return {
      zhuanchang:{},
      zhuanchangList:[],
      goods:[],
      listHeight:[],
      menuScroll:{},
      foodScroll:{},
      scrollY:0, //滚动的高度
      poi_info:{},
      selcetshow:{}
    }
  },
  created(){
  	fetch("api/goods").then(res=>{
      return res.json()
    }).then(response=>{
      console.log(response)
      if(response.code==0){
      	this.poi_info = response.data.poi_info
        this.zhuanchang = response.data.container_operation_source
        this.zhuanchangList = response.data.container_operation_source.operation_source_list
        this.goods = response.data.food_spu_tags
        console.log(888,this.goods)
      }
    })
  },
 mounted(){
 	setTimeout(()=>{
	 	this.$nextTick(()=>{
	  		this.calculateHeight()
	  	})
 	},500)
 },
  computed:{
  	currentIndex(){
  		for(let index in this.listHeight){
  			if(this.listHeight[index]>this.scrollY && this.listHeight[index-1]<=this.scrollY){
  			return index -1
  			}
  		}return 0
  	},
  	selectFoods(){
  		let foods = []
  		this.goods.forEach((myfoods)=>{
  			myfoods.spus.forEach((food)=>{
  				if(food.count > 0){
  					foods.push(food)
  				}
  			})
  		})
  		return foods
  	} 
  },
  methods:{
  	calculateHeight(){
  		let foodlist = this.$refs.foodScroll.getElementsByClassName('food-list-dot')
  		// console.log(2323,foodlist)
  		let height=0
  		this.listHeight.push(height)
  		for(let item of foodlist){
  			// console.log(item.clientHeight)
  			height+=item.clientHeight
  			this.listHeight.push(height)
  		}
  		console.log('listh',this.listHeight)
  	},
  	listScroll(){
  		this.scrollY = this.$refs.foodScroll.scrollTop
  	},
  	tap(cIndex){
  		let foodlist = this.$refs.foodScroll.getElementsByClassName('food-list-dot')
  			foodlist[cIndex].scrollIntoView()
  	},
  	calculateCount(spus){
      let count = 0
      spus.forEach((food) => {
        if(food.count > 0){
          count += food.count
        }
      })
      return count
    },
    showProduct(item){
    	this.$refs.proDetail.showproduct()
    	this.selcetshow = item
    }
  }
}
</script>
<style scoped lang="scss">
.leftnav{
	display:block;
	width: 5.5rem;
	overflow-y: auto;
	scroll-behavior: smooth;
	li{
		position: relative;
		display: block;
		padding: 10px;
		text-align: center;
		font-size: 0.888889rem;
		span{
			position: absolute;
			top: 0;
			right: 2px;
			width: 0.9rem;
			height: 0.9rem;
			line-height: 0.9rem;
			border-radius: 50%;
			background: $red;
			color: #fff;
			font-size: 0.777778rem;
		}
	*{
		font-size: 0.888889rem;
	}
	img{
	width: 1.222222rem;
	height: 1.222222rem;
		}
	}
	.on{
	background: #ddd;
	} 

}
.rightview{
	overflow-y: auto;
	scroll-behavior: smooth;
	background: #fff;
	padding-bottom: 5.222222rem;
	*{
		font-size: 1rem;
	}
	.tit{
		border-left: 4px solid $cmain;
		padding-left: 0.555556rem;
		font-weight: bold;
	}
	.topimg{
	width:100%;
	height: 5.555556rem;
	margin-bottom: 5px;
	overflow: hidden;
	border-radius: 5px;
	img{
		width:100%;
		height: 5.555556rem;
		object-fit:cover;
		}
	}
	.item{
		padding: 10px;
	}
	.goodsimg{
		width: 4.444444rem;
		height: 4.444444rem;
		img{
			width:100%;
			height:100%;
			object-fit:cover;
		}
	}
	.title{
		font-weight: bold;
	}
}
@media screen and (min-width: 1024px){
  .leftnav, .rightview{
  	height: 56rem;
  }
 }
@media screen and (max-width: 768px){
  .leftnav, .rightview{
  	height: 38rem;
  }
  .leftnav{
  	width: 6.5rem;
  }
 }
@media screen and (max-width: 420px){
 .leftnav, .rightview{
  	height: 36rem;
  }
}
@media screen and (max-width: 380px){
  .leftnav, .rightview{
  	height: 36rem;
  }
  .leftnav{
  	width: 5.2rem !important;
  }
}
@media screen and (max-width: 330px){
  .leftnav, .rightview{
  	height: 32rem;
  }
}
</style>


