<template>
  <div class="shopcart">
  	<div class="pop" v-show="fold" @click="toggle"></div>
  	<!-- 购物车 -->
  	<div class="gouwu dis-box justify-content-center" :class="{on:totalCount}" @click="toggle">
		<i class="iconfont icon-gouwuche1 fs26"></i>
		<span v-show="totalCount">{{totalCount}}</span>
	</div>
	<div class="right dis-box justify-content-between">
		<div class="pl-50">
			<div v-show="totalPrice" class="tprice">￥{{totalPrice}}</div>
			<div class="fs12">另需{{this.poi_info.shipping_fee_tip}}</div>	
		</div>
		<div v-show="!totalPrice" class="fs16 pr-30">￥8元起送</div>
		<div v-show="totalPrice" class="jiesuan dis-box justify-content-center flex-direction-column">
			<span>去结算</span>
		</div>
	</div>
	<!-- 购物车列表 -->
	<div class="cartview" v-show="clistShow">
	  	<div class="fs12 ta-c bg-main pd-5">新用户立减17元</div>
	  	<div class="clear dis-box justify-content-between">
	  		<div class="tit">1号口袋</div>
	  		<div class="iconfont icon-lajixiang mr-10 " @click="clearCart">
	  			<span class="ml-10">清空购物车</span>
	  		</div>
	  	</div>
	  	<ul class="selfood">
	  		<li class="dis-box justify-content-between" v-for="item in selectFoods"> 
	  			<div class="leftitem">
	  				<div class="overflow-line-1">{{item.name}}</div>
	  			</div>
	  			<div class="dis-box">
	  				<div class="pr-50">￥{{item.min_price}}</div>
					<cart-control :food="item" class="pr-10">1</cart-control>
	  			</div>
	  		</li>
	  	</ul>
	</div>

  </div>
</template>
<script>
import CartControl from '../cartcontrol/CartControl'
export default {
	components:{
		'cart-control':CartControl
	},
	props:{
		poi_info:{
			type: Object,
			default:{}
		},
		selectFoods:{
			type: Array,
			default:[]
		},

	},
	computed:{
		totalCount(){
			let num = 0
			this.selectFoods.forEach((food)=>{
				num += food.count
			})
			return num
		},
		totalPrice(){
			let total = 0
			this.selectFoods.forEach((food)=>{
				total += food.count*food.min_price
			})
			return total
		},
		clistShow(){
			if(!this.totalCount){
				this.fold =false
				return false
			}else{
				let w = this.fold 
				return w
			}
		}
	},
	methods:{
		toggle(){
			if(!this.totalCount){
				this.fold = false
				return
			}else{
				this.fold =! this.fold
			}
		},
		clearCart(){
			this.selectFoods.forEach((item)=>{
				item.count = 0
			})
		}
	},
	data () {
	return {
		fold:false,
		}
	}
}
</script>
<style lang="scss">
.shopcart{
	position: fixed;
	z-index:6;
	bottom:0;
	background: #444;
	width:100%;
	height: 3rem;
	padding-left: 10px;
	*{
		font-size: 0.777778rem;
	}
	.pop{
		position:fixed;
		top:0;
		left:0;
		width:100%;
		height:100%;
		opacity:0.7;
		z-index:-2;
		background: #444;
	}
	.cartview{
		box-sizing: border-box;
		position:absolute;
		bottom:3rem;
		left:0;
		z-index:-1;
		opacity:0.9;
		width:98%;
		background: #fff;
		color: black;
		.clear{
			background: #ddd;
			padding: 5px;
			*{
				font-size: 0.777778rem;
			}
		}
		.selfood{
			li{
				border-bottom: 1px solid #eee;
			}
			*{
				font-size: 1rem;
			}
			padding: 0.833333rem;
			max-height: 18rem;
			overflow: auto;
			.leftitem{
			height: 4rem;
			line-height: 4rem; 
			width: 7rem;
			white-space: nowrap;

			}
		}
		
	}
	.gouwu{
		position:absolute;
		top: -1rem;
		width: 2.5rem;
		height: 2.5rem;
		background: #666;
		color:#ddd !important;
		border-radius:50%;
		&.on{
			background: $cmain;
			i{
				color: black;
			}
		}
		span{
			position: absolute;
			top: 0;
			left: 2rem;
			width: 0.9rem;
			height: 0.9rem;
			line-height: 0.9rem;
			text-align: center;
			border-radius:50%;
			background: $red;
		}
	}
	.right{
		height: 100%;
		*{
			font-size: 1rem;
			color:#fff;
		}
		.tprice{
			font-size: 1.333333rem;
		}
		.jiesuan{
			width:110px;
			height:100%;
			line-height:100%;
			background: $cmain;
		}
	}
}
</style>



