<template>
  <div class="cartcontrol dis-box justify-content-between">
	<i class="iconfont icon-jianhao c9 fw-b fs24" @click.stop.prevent="decrease" v-show="food.count"></i>
	<span class="plr-15" v-show="food.count">{{food.count}}</span>
  	<i class="iconfont icon-jiahao c-main fw-b fs24" @click.stop.prevent="increase"></i>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  props:{
  	food:{
  		type:Object,
  	}
  },
  methods:{
  	decrease(){
  		this.food.count --
  	},
  	increase(){
  		if(!this.food.count){
  			Vue.set(this.food,"count",1)
  		}else{
  			this.food.count ++
  		}
  	
  	}
  }
}
</script>
<style lang="scss">

</style>



